@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,500;0,700;1,100&display=swap);
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: rgb(249, 249, 249);
}

* {
  font-family: 'Roboto', sans-serif;
}





* {
    box-sizing: border-box;
}
header {
    background-color: black;
    padding: 10px;
    justify-content: center;
    position:relative;
    z-index:5;
}

.main-nav {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
}

.bike-logo-bttn {
    width: 100px;
    padding: 10px;
    margin-left: 35px;
}


.bike-logo-bttn img {
    width: 100px;
}

.shop-bttn {
    color: black;
    background-color: #4AC0E0;
    padding: 5px;
    height: 30px;
    width: 5em;
    font-size: 18px;
    font-weight: 500;
    text-align: center;
    line-height: 18px;
    margin-right: 2.5em;
}

#search {
    font-size: 14px;
    outline: none;
    color: #ffffff;
    border: none;
    height: 25px;
    width: 400px;
    background: transparent; 
    border-bottom: solid 1px #4AC0E0;
    padding-right: 35px;
}

#search-icon {
    width: 25px;
    position: relative;
    left: -30px;
}

.cart-ctn {
    display: flex;
    align-items: center;
}
.cart-logo-bttn {
    width: 40px;
    position: relative;
    margin-left: 35px;
  }

.cart-logo-bttn p {
    position: absolute;
    background-color: darkgoldenrod;
    border-radius: 50%;
    text-align: center;
    line-height: 20px;
    height: 20px;
    width: 20px;
    top: -18px;
    left: -8px;
}

.cart-logo-bttn:hover {
    cursor: pointer;
}

.cart-logo-bttn img {
    width: 40px;
}

.cart-drawer {
    background-color: #556e75;
    
    width: 400px;
    position: fixed;
    z-index: 10;
    top: 0;
    right: -420px;
    height: 100vh;
    transition: all .3s ease-in-out;
    padding: 10px 30px;
}

.cart-drawer button {
    width: 100%;
    height: 40px;
    border-radius: 4px;
    border: none;
}

.cart-drawer-nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 20px 0px;
    border-bottom: 1px solid black;
}

.cart-drawer-nav div {
    font-weight: 700;
    font-size: 1.5em;
    margin-right: 20px;
}

.cart-drawer-nav div:hover {
    cursor: pointer;
}

.cart-drawer-nav h4 {
    margin: 0px;
}

.cart-drawer.active {
    right: 0px;
}

.cart-display img {
    width: 50px;
    border-radius: 5px;
}

.cart-drawer-item {
    border-bottom: 2px solid grey;
    margin-bottom: 20px;
}

.cart-drawer-item p {
    font-weight: bold;
}

@media screen and (max-width: 1100px) {
    #search,
    #search-icon {
        display: none;
    }

    .main-nav {
        justify-content: center;
    }


    .bike-logo-bttn {
        margin-right: 40px;
    }
}

@media screen and (max-width: 350px) {
    #search,
    #search-icon {
        display: none;
    }

    .main-nav {
        justify-content: center;
    }


    .cart-logo-bttn {
        margin-left: 0px;
    }
}
.home-ctn {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.background-img {
    /* Set rules to fill background */
    min-height: 100%;
    min-width: 1920px;
      
    /* Set up proportionate scaling */
    width: 100%;
    height: auto;
      
    /* Set up positioning */
    position: fixed;
    top: 100px;
    left: 0;
  }
  
.home-ctn h3 {
  position: relative;
  z-index: 3;
  color: #ffffff;
}
.home-title {
  position: relative;
  z-index: 3;
  color: #ffffff;
  font-size: 5em;
  margin-top: 2em;
  letter-spacing: 5px;
  margin-bottom: 10px;
}

@media screen and (max-width: 1920px) { /* Specific to this particular image */
  .background-img {
    left: 50%;
    margin-left: -960px;   /* 50% */
  }
}

.product-page {
    padding: 20px;
    display: flex;
    justify-content: center;
}

.img-ctn {
    width: 500px;
    border: 1px solid rgb(206, 206, 206);
    margin-right: 30px;
}

.product-page img {
    height: auto;
    width: 500px;
}

.add-to-cart {
    background-color: rgb(0, 0, 0);
    color: #4AC0E0;
    padding: 10px;
    font-weight: 700;
    text-align: center;
    transition: .1s;
}

.add-to-cart:hover {
    background-color: teal;
}
.add-to-cart:active {
    background-color: black;
}

.qty-selector-ctn {
    margin: 10px;
    display: flex;
    border: .5px solid grey;
    height: 30px;
    border-radius: 2px;
}

.qty-selector-ctn span {
    width: 45px;
    text-align: center;
    font-size: 14px;
    line-height: 28px;
    height: 30px;

}
.qty-selector-ctn button {
    border: transparent;
    width: 30px;
    height: 25px;
    font-size: 22px;
    line-height: 22px;
}

.shop-page-content {
    display: flex;
    padding: 15px 30px;
    margin: 30px 0px 250px 0px;
}

a {
    text-decoration: none;
}

.product-card-ctn {
    display: flex;
    flex-wrap: wrap;
    max-width: 1500px;
    justify-content: center;
    align-items: center;
    margin: 0 auto;

}

.product-card-ctn > * {
    flex: 1 1;
    min-width: 250px;
    max-width: 275px;
}

.product-card-ctn a {
    margin: 10px;
}

.product-card {
    background-color: rgb(255,255,255);
    flex: 1 1;
    flex-basis: 300px;
    max-width: 300px;
    height: 380px;
    padding: 10px;
    border-radius: 10px;
    box-shadow: 0 0 0  ;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.product-card-detail {
    display: flex;
    flex-direction: column;
    align-items: center;
   
}
.product-card-detail h4 {
    margin: 0;
}

.product-card img {
    width: 100%;
    height: auto;
}

.product-card h4,
.product-card p {
    color: black;
    margin: 0;
}


.filter-bar {
    border: 1px solid green;
}

@media screen and (max-width: 350px) {
    .product-card-ctn > * {
        flex: 1 1;
        min-width: 150px;
        max-width: 275px;
    }

    .shop-page-content {
        padding: 0;
        margin: 0px;
    }

    .product-card {
        height: auto;
    }
}
